body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.result {
  margin-left: 50px;
  margin-right: 50px;
  padding-top: 20px;
  border-top: 2px solid #ddd;
  text-align: center;
  font-size: 27px;
}

.result h3,
.result h4 {
  font-weight: normal;
}
.result em {
  font-weight: bold;
}

html {
  min-height: 100vh;
  background: linear-gradient(
    to bottom,
    rgb(253, 72, 0) 0%,
    rgb(248, 232, 92) 100%
  );
}
.App input,
.App button {
  margin: 20px;
  padding: 10px 20px;
  border: 2px solid gray;
  background-color: rgb(128, 211, 139);
  transition: 0.3s;
  border-radius: 10px;
  font-size: 22px;
}

.App button {
  cursor: pointer;
}
.App button:hover {
  background-color: #000;
  color: white;
  border-color: black;
}
form {
  margin-left: 30vw;
  margin-right: 10vw;
}
.App input {
  width: 35%;
}

